import Vue from 'vue'
import Router from 'vue-router'
import Index from './views/Index.vue'

Vue.use(Router)

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '',
      name: 'index',
      component: Index
    },
    {
      path: '/home',
      name: 'home',
      component: () => import('./views/Home.vue')
    },
    {
      path: '/time',
      name: 'time',
      component: () => import('./views/Time.vue')
    },
    {
      path: '/appoint',
      name: 'appoint',
      component: () => import('./views/Appoint.vue')
    },
    {
      path: '/text',
      name: 'text',
      component: () => import('./components/Text.vue')
    },
    {
      path: '/result',
      name: 'result',
      component: () => import('./views/Result.vue')
    },
    {
      path: '/query',
      name: 'query',
      component: () => import('./views/Query.vue')
    },
    {
      path: '/information',
      name: 'information',
      component: () => import('./views/Information.vue')
    },
    {
      path: '/resultlist',
      name: 'resultlist',
      component: () => import('./views/ResultList.vue')
    }
    
  ]
})
